import React from 'react';
import { useNavigate } from 'react-router-dom'; // React Router useNavigate 
import KakaoLoginButton from './KakaoLoginButton'; // KakaoLoginButton  import
import './StartPage.css';
import axios from 'axios';

const StartPage = () => {
    const navigate = useNavigate(); // useNavigate    

    const handleClick = (loginType) => {
        axios
            .get('http://13.125.0.218:8000/login', {
                params: { type: loginType }
            })
            .then(response => {
                console.log(response.data);
                //    
                if (loginType === 'kakao') {
                    if (response.data.is_first_login) {
                        navigate('/register'); //    RegisterPage 
                    } else {
                        navigate('/main'); //   HomePage 
                    }
                }
                else if(loginType == 'google'); {
                    if (response.data.is_first_login) {
                        navigate('/register'); //   BasicRegist 
                    } else {
                        navigate('/main'); //  HomePage 
                    }

                }
            })
            .catch(error => {
                console.error('There was an error!', error);
            });
    };

    return (
        <div className="startpage-container">
            <div className="startpage-content">
                <div className="title">
                    <div></div>
                    <div></div>
                    <div></div>
                    <div></div>
                </div>
                <div className="login-options">
                    { <div className="login-option google" onClick={() => navigate('login')}>
                        <span className="text"></span>
                    </div>}
                    <KakaoLoginButton /> {/* KakaoLoginButton   */}
                    {/* <div className="login-option naver" onClick={() => handleClick('naver')}>
                        <span className="icon">N</span>
                        <span className="text"> </span>
                    </div> */}
                </div>
            </div>
        </div>
    );
};

export default StartPage;
