import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import './MainPage.css'; // CSS  import

const MainPage = () => {
    const navigate = useNavigate();
    const [userName, setUserName] = useState(''); //   

    useEffect(() => {
        //    
        const fetchUserInfo = async () => {
            try {
                const response = await fetch('http://13.125.0.218:8000/api/auth/', {
                    headers: {
                        'Content-Type': 'application/json',
                    },
                    credentials: 'include', //   
                });

                if (response.ok) {
                    const data = await response.json();
                    setUserName(data.first_name); //    
                } else {
                    console.error('Failed to fetch user info');
                }
            } catch (error) {
                console.error('Error fetching user info:', error);
            }
        };

        fetchUserInfo();
    }, []);

    const handleLogoClick = () => {
        navigate('/more');
    };

    return (
        <div className="main-container">
            <header className="header">
                <div className="header-text"></div>
                <div
                    className="header-logo"
                    onClick={handleLogoClick}
                >
                    <div className="burger-icon"></div>
                    <div className="burger-icon"></div>
                    <div className="burger-icon"></div>
                </div>
            </header>

            <div className="greeting-box">
                <div className="greeting-text">
                    <span className="greeting-highlight">. </span>
                    <span className="greeting-name">{userName}</span>
                    <span className="greeting-highlight">.<br />   ?</span>
                </div>
            </div>

            <div className="diary-sections">
                <div className="diary-section" onClick={() => navigate('/diary')}> </div>
                <div className="diary-section" onClick={() => navigate('/storage')}> </div>
                <div className="diary-section" onClick={() => navigate('/usage')}> </div>
            </div>
        </div>
    );
};

export default MainPage;
