// src/components/main/MoreMenu.js
import React from 'react';
import { useNavigate } from 'react-router-dom';
import { Button } from "antd";
import { useBlueLight } from '../contexts/BlueLightContext'; // Context  import
import './MoreMenu.css';

const MoreMenu = () => {
    const { blueLight, toggleBlueLight } = useBlueLight(); // Context 
    const navigate = useNavigate();

    const goToMainPage = () => {
        navigate('/main');
    };

    return (
        <div className="more-menu-container">
            <div className={`blue-light-filter ${blueLight ? 'active' : ''}`} />
            <header className="header">
                <div className="header-text"></div>
                <div
                    className="header-logo"
                    onClick={goToMainPage}
                >
                    <div className="burger-icon"></div>
                    <div className="burger-icon"></div>
                    <div className="burger-icon"></div>
                </div>
            </header>
            <div className="menu-content">
                <div className="menu-item switch" onClick={toggleBlueLight}>
                    <div className="switch-container">
                        <div className={`switch ${blueLight ? 'active' : ''}`}></div>
                        <span className="switch-text">
                              {blueLight ? '' : ''}
                        </span>
                    </div>
                </div>
                <Button type="primary" className="menu-item"> </Button>
                <Button type="primary" className="menu-item"></Button>
                <Button type="primary" className="menu-item">  </Button>
            </div>
        </div>

        
    );
};

export default MoreMenu;