import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import './SettingPage.css';

function SettingPage() {
    const [formData, setFormData] = useState({
        name: '',
        phone: '',
        location: '',
        ownership: false,
        alarm: '',
        eyeMode: false,
    });

    const navigate = useNavigate();

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData((prevData) => ({
            ...prevData,
            [name]: value,
        }));
    };

    const handleToggle = (field) => {
        setFormData((prevData) => ({
            ...prevData,
            [field]: !prevData[field],
        }));
    };

    const handleWatchConnect = async () => {
        try {
            const response = await axios.post('http://13.125.0.218:8000/api/connect-watch/', {}, {
                headers: {
                    'Content-Type': 'application/json',
                },
            });
            alert(response.data.message);
        } catch (error) {
            alert('  .');
        }
    };

    const handleSubmit = async () => {
        try {
            //    
            await axios.post('http://localhost:8000/api/set-alarm/', {
                alarm_time: formData.alarm,
            });

            // Diary      
            navigate('/diary', { state: { alarm: formData.alarm } });
        } catch (error) {
            console.error('Failed to save alarm time:', error);
            alert('    .  .');
        }
    };

    return (
        <div className="App">
            <div className="container">
                <div className="header">
                    <h1> </h1>
                </div>
                <form>
                    <label>
                        
                        <input
                            type="text"
                            name="name"
                            value={formData.name}
                            onChange={handleChange}
                            placeholder=" "
                        />
                    </label>
                    <label>
                         
                        <input
                            type="text"
                            name="phone"
                            value={formData.phone}
                            onChange={handleChange}
                            placeholder="  "
                        />
                    </label>
                    <label>
                        
                        <input
                            type="text"
                            name="location"
                            value={formData.location}
                            onChange={handleChange}
                            placeholder=" "
                        />
                    </label>
                    <label>
                          
                        <div className="watch-container">
                            <button
                                type="button"
                                className="toggle-button"
                                onClick={() => handleToggle('ownership')}
                            >
                                {formData.ownership ? '' : ''}
                            </button>
                            {formData.ownership && (
                                <button
                                    type="button"
                                    className="toggle-button"
                                    onClick={handleWatchConnect}
                                    style={{ marginLeft: '10px' }}
                                >
                                    
                                </button>
                            )}
                        </div>
                    </label>
                    <label>
                         ()  
                        <input
                            type="time"
                            name="alarm"
                            value={formData.alarm}
                            onChange={handleChange}
                            placeholder="  "
                        />
                    </label>
                    <label>
                           
                        <div className="watch-container">
                            <button
                                type="button"
                                className="toggle-button"
                                onClick={() => handleToggle('eyeMode')}
                            >
                                {formData.eyeMode ? '' : ''}
                            </button>
                        </div>
                    </label>
                    <label>
                         
                        <div className="connected-account">
                            <div>
                                <span>N</span>  
                            </div>
                        </div>
                    </label>
                    <button type="button" onClick={() => console.log('   ')}>  </button>
                    <button type="button" onClick={() => console.log('   ')}>  </button>
                    <button type="button" onClick={handleSubmit}>
                        Diary   
                    </button>
                </form>
            </div>
        </div>
    );
}

export default SettingPage;
