// PreviousDiary.js
import React, { useState, useEffect } from 'react';
import Calendar from 'react-calendar'; // react-calendar  
import axios from 'axios'; // axios import
import './PreviousDiary.css'; // CSS  import

const PreviousDiary = () => {
    //   
    const [date, setDate] = useState(new Date());
    const [userID, setUserID] = useState(null); // userID  
    const [diaryEntries, setDiaryEntries] = useState([]); //    

    //  ID  
    const fetchUserID = async () => {
        try {
            const response = await axios.get('http://13.125.0.218:8000/auth/login'); //  ID  API 
            setUserID(response.data.userID); // userID  
        } catch (error) {
            console.error('Error fetching user ID:', error);
        }
    };

    //     
    const fetchDiaryEntries = async () => {
        if (userID) {
            try {
                const response = await axios.get(`/api/diary/${userID}`, {
                    params: { date: date.toISOString().split('T')[0] }, //      
                });
                setDiaryEntries(response.data.entries); //    
            } catch (error) {
                console.error('Error fetching diary entries:', error);
            }
        }
    };

    //     ID .
    useEffect(() => {
        fetchUserID();
    }, []);

    //      .
    useEffect(() => {
        fetchDiaryEntries();
    }, [date, userID]);

    const handleDateChange = (newDate) => {
        setDate(newDate);
        //         
    };

    return (
        <div className="calendar-container">
            <div className="calendar-header">
                <button className="arrow-button" onClick={() => handleDateChange(new Date(date.getFullYear(), date.getMonth() - 1))}>
                    &lt;
                </button>
                <div className="calendar-title">
                    {date.toLocaleDateString('default', { month: 'long', year: 'numeric' })}
                </div>
                <button className="arrow-button" onClick={() => handleDateChange(new Date(date.getFullYear(), date.getMonth() + 1))}>
                    &gt;
                </button>
            </div>
            <Calendar
                className="react-calendar"
                value={date}
                onChange={handleDateChange}
            />
            {/*     */}
            <div className="diary-entries">
                {diaryEntries.length > 0 ? (
                    <ul>
                        {diaryEntries.map((entry, index) => (
                            <li key={index}>{entry.content}</li> //  , entry.content  
                        ))}
                    </ul>
                ) : (
                    <p>No diary entries available.</p>
                )}
            </div>
        </div>
    );
};

export default PreviousDiary;
